import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';

import style from './style.css';

function getTicks(count, min, max, prefix = '', postfix = '') {
    const loop = Array.from(Array(count).keys());
    const range = Math.ceil(max) - Math.floor(min);
    return loop.map(d => `${prefix}${parseInt((range / (count - 1) * d), 10) + Math.floor(min)}${postfix}`);
}

function parseTicks(ticks) {
    if (ticks) {
        return JSON.parse(ticks);
    }
    return undefined;
}

function transformXTick(tick) {
    if (typeof tick === 'string') {
        if (tick.match(/\d+h/)) {
            const msDiff = tick.replace(/h/g, '') * 3600 * 1000;
            const now = util.epoch();
            const then = now - msDiff;
            return util.isoDate(then);
        }
    }
    return tick;
}

function normalizeRange(val, min, max, newMin, newMax) {
    return newMin + (val - min) * (newMax - newMin) / (max - min);
}

function round5(x, roundDown = false) {
    let sign = 1;
    if (x < 0) {
        sign = -1;
    }
    if (roundDown && sign === 1) {
        return Math.floor(Math.abs(x) / 5) * 5 * sign;
    }
    return Math.ceil(Math.abs(x) / 5) * 5 * sign;
}

function findMaxY({ data, max }) {
    if (util.isNumber(max)) {
        return Math.max(max, ...data.map(d => d.y));
    }
    return Math.max(...data.map(d => d.y));
}

function findMinY({ data, min }) {
    if (util.isNumber(min)) {
        return Math.min(min, ...data.map(d => d.y));
    }
    return Math.min(...data.map(d => d.y));
}


function smoothValues(arr, windowSize = 2, getter = (value) => value, setter) {
    const get = getter
    const result = []
    for (let i = 0; i < arr.length; i += 1) {
        const leftOffeset = i - windowSize
        const from = leftOffeset >= 0 ? leftOffeset : 0
        const to = i + windowSize + 1
        let count = 0
        let sum = 0
        for (let j = from; j < to && j < arr.length; j += 1) {
            sum += get(arr[j])
            count += 1
        }
        result[i] = setter ? setter(arr[i], sum / count) : sum / count
    }
    return result
}


// Properties of a line 
// I:  - pointA (array) [x,y]: coordinates
//     - pointB (array) [x,y]: coordinates
// O:  - (object) { length: l, angle: a }: properties of the line
function lineProperties(pointA, pointB) {
    const lengthX = pointB[0] - pointA[0];
    const lengthY = pointB[1] - pointA[1];
    return {
        length: Math.sqrt(Math.pow(lengthX, 2) + Math.pow(lengthY, 2)),
        angle: Math.atan2(lengthY, lengthX),
    };
}

// Position of a control point 
// I:  - current (array) [x, y]: current point coordinates
//     - previous (array) [x, y]: previous point coordinates
//     - next (array) [x, y]: next point coordinates
//     - reverse (boolean, optional): sets the direction
// O:  - (array) [x,y]: a tuple of coordinates
function controlPoint(current, previous, next, reverse) {
    // When 'current' is the first or last point of the array
    // 'previous' or 'next' don't exist.
    // Replace with 'current'
    const p = previous || current;
    const n = next || current;
    
    // The smoothing ratio
    const smoothing = 0.2;
    
    // Properties of the opposed-line
    const o = lineProperties(p, n);
    
    // If is end-control-point, add PI to the angle to go backward
    const angle = o.angle + (reverse ? Math.PI : 0);
    const length = o.length * smoothing;
    
    // The control point position is relative to the current point
    const x = current[0] + Math.cos(angle) * length;
    const y = current[1] + Math.sin(angle) * length;
    return [x, y]
}

// Create the bezier curve command 
// I:  - point (array) [x,y]: current point coordinates
//     - i (integer): index of 'point' in the array 'a'
//     - a (array): complete array of points coordinates
// O:  - (string) 'C x2,y2 x1,y1 x,y': SVG cubic bezier C command
function bezierCommand(point, i, a) {
    // start control point
    const [cpsX, cpsY] = controlPoint(a[i - 1], a[i - 2], point)  // end control point
    const [cpeX, cpeY] = controlPoint(point, a[i - 1], a[i + 1], true);
    return `C ${cpsX},${cpsY} ${cpeX},${cpeY} ${point[0]},${point[1]}`
}

// Svg path line command
// I:  - point (array) [x, y]: coordinates
// O:  - (string) 'L x,y': svg line command
function lineCommand(point) {
    return `L ${point[0]} ${point[1]}`;
}

// Render the svg <path> element 
// I:  - points (array): points coordinates
//     - command (function)
//       I:  - point (array) [x,y]: current point coordinates
//           - i (integer): index of 'point' in the array 'a'
//           - a (array): complete array of points coordinates
//       O:  - (string) a svg path command
// O:  - (string): a Svg <path> element
function svgPath(points, command) {
    // build the d attributes by looping over the points
    const d = points.reduce((acc, point, i, a) => i === 0
        // if first point
        ? `M ${point[0]},${point[1]}`
        // else
        : `${acc} ${command(point, i, a)}`
    , '');
    
    return d;
}

function makePath({ data, yMin, yMax, width, height, yRangeMin, yRangeMax, offsetY = 0, maxMinFixed = false, smooth = true }) {
    const dataMap = {};
    if (data && data.length) {
        const maxX = Math.max(...data.map(d => d.x));
        let minY;
        let maxY;
        if (maxMinFixed) {
            minY = util.isNumber(yMin) ? yMin : findMinY({ data });
        } else {
            minY = util.isNumber(yMin) ? findMinY({ data, min: yMin }) : findMinY({ data });
            minY = round5(minY, true);
        }
        if (maxMinFixed) {
            maxY = util.isNumber(yMax) ? yMax : findMaxY({ data });
        } else {
            maxY = util.isNumber(yMax) ? findMaxY({ data, max: yMax }) : findMaxY({ data });
            maxY = round5(maxY);
        }

        const realHeight = height - offsetY;
        const fnX = (val, idx) => {
            const x = Math.round(val / maxX * width);
            if (util.isDefined(idx)) {
                if (!dataMap[idx]) { dataMap[idx] = {}; }
                dataMap[idx].x = x;
                dataMap[idx].xVal = val;
            }
            return x;
        }

        const fnY = (val, idx) => {
            let dataVal = val;
            if (util.isNumber(yRangeMin) && util.isNumber(yRangeMax)) {
                dataVal = normalizeRange(dataVal, minY, maxY, yRangeMin, yRangeMax);
                return realHeight - normalizeRange(dataVal, yRangeMin, yRangeMax, offsetY, height);
            }
            dataVal = normalizeRange(dataVal, minY, maxY, minY, maxY);
            const y = Math.round(realHeight - normalizeRange(dataVal, minY, maxY, offsetY, height));
            if (util.isDefined(idx)) {
                if (!dataMap[idx]) { dataMap[idx] = {}; }
                dataMap[idx].y = y;
                dataMap[idx].yVal = dataVal;
            }
            return y;
        };

        const dataArray = data.map((p, idx) => [fnX(p.x, idx + 1), fnY(p.y, idx + 1)]);
        const finalDataArray = smoothValues(dataArray, 2, (e) => e[1], (e, val) => { e[1] = val; return e; });

        for (let i = 0, l = finalDataArray.length; i < l; i += 1) {
            if (util.isDefined(dataMap[i])) {
                dataMap[i].ySmooth = finalDataArray[i][1];
            }
        }

        const d = svgPath(finalDataArray, smooth ? bezierCommand : lineCommand);
        return { d, maxX, maxY, minY, dataMap };
    }
    return {};
}

const initialState = {
    mouseData: {},
};

@observer
class LineChart extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
        this.svgRef = null;
    }

    mouseMove = (e) => {
        if (this.props.stores) {
            const { width = 600, height = 200 } = this.props;
            const svgWidth = this.svgRef.clientWidth;
            const svgHeight = this.svgRef.clientHeight;

            const x = parseInt(normalizeRange(e.layerX, 30, svgWidth, 0, width), 10);
            const y = parseInt(normalizeRange(e.layerY, 20, svgHeight, 0, height) + 20, 10);

            const { appState } = this.props.stores;
            appState.setMousePosition([x, y]);
        }
    };

    getMouseData(dataMap, xPos, key, autoLimit = false, padding = 0) {
        const { data = [], width = 600, height = 200 } = this.props;
        const x = parseInt(normalizeRange(xPos, 0, width, 0, data.length), 10);
        // console.log(`xPos=${xPos}, svgWidth=${svgWidth}, data.length=${data.length}`)
        if (util.checkNested(dataMap, x, key)) {
            // console.log(`dataMap[x]=${JSON.stringify(dataMap[x])}, xPos=${xPos}, x=${x}, key=${key}`);
            let val = dataMap[x][key];
            if (autoLimit && key === 'y') {
                if (val > height / 2) {
                    return val - 70 - padding;
                }
                return val + 20 - padding;
            }
            if (autoLimit && key === 'x') {
                if (val > width / 2) {
                    // console.log(`autoLimit=${autoLimit}, key=${key}, val=${val}, width=${width}`)
                    return val - 160 - padding;
                }
                return val + 20 - padding;
            }
            return val;
        }
    }


    render() {
        const {
            width = 600,
            height = 200,
            offsetY = 0,
            paddingLeft = 0, // Make room for yTicks.
            paddingBottom = 0, // Make room for xTicks.
            tickCount = 6, // Number of ticks to show
            showXTicks, // Show X ticks
            showYTicks, // Show Y ticks
            xTicks, // xTicks array to use instead of values. JSON.stringified and backslash escaped.
            yTicks, // yTicks array to use instead of values. JSON.stringified and backslash escaped.
            yTicksPrefix,
            yTicksPostfix,

            yMax, // Y max value to use for yTicks
            yMin, // Y min value to use for yTicks

            showY2Ticks, // Show Y ticks
            y2Ticks, // yTicks array to use instead of values. JSON.stringified and backslash escaped.
            y2TicksPrefix,
            y2TicksPostfix,
            y2Max, // Y max value to use for y2Ticks
            y2Min,

            y2BMax, // Y max value to use for y2TicksB
            y2BMin, // Y max value to use for y2TicksB
            range2bMin = 0,
            range2bMax = 100,

            y2CMax, // Y max value to use for y2TicksC
            y2CMin, // Y max value to use for y2TicksC
            range2cMin = 0,
            range2cMax = 100,

            y2DMax, // Y max value to use for y2TicksC
            y2DMin, // Y max value to use for y2TicksC
            range2dMin = 0,
            range2dMax = 100,

            y3Min,
            y3Max, // Y max value to use for yTicks3

            range3Min = 0,
            range3Max = 100,

            legend,
            legendB,
            legendC,
            legendD,
            legend2,
            legend2B,
            legend2C,
            legend2D,
            legend3,

            data = [],
            dataB = [],
            dataC = [],
            dataD = [],
            data2 = [],
            data2B = [],
            data2C = [],
            data2D = [],
            data3 = [],

            marker,
            unit,
            unitB,
            unitC,
            unitD,
            unit2,
            unit2B,
            unit2C,
            unit2D,
        } = this.props;
        const { appState = {} } = this.props.stores || {};
        const { mousePos = [], darkmode } = appState;

        const PADDING_LEFT = paddingLeft || (showYTicks ? 30 : 0);
        const PADDING_BOTTOM = paddingBottom || (showXTicks ? 20 : 0);

        const common = {
            width,
            height,
            offsetY,
        };

        // console.table(xTicks);
        // console.table(yTicks);
        const dataMaxY = findMaxY({ data, max: yMax });
        const dataBMaxY = findMaxY({ data: dataB, max: dataMaxY });
        const MAX_Y = round5(dataBMaxY);

        const dataMinY = findMinY({ data, min: yMin });
        const dataBMinY = findMinY({ data: dataB, min: dataMinY });
        const MIN_Y = round5(dataBMinY, true);

        const {
            d,
            maxX: MAX_X = 0,
            dataMap,
        } = makePath({
            ...common,
            data,
            yMin: MIN_Y,
            yMax: MAX_Y,
            maxMinFixed: true,
            smooth: false,
        });

        const { d: dB, dataMap: dataMapB } = makePath({
            ...common,
            data: dataB,
            yMin: MIN_Y,
            yMax: MAX_Y,
            maxMinFixed: true,
            smooth: false,
        });

        const { d: dC, dataMap: dataMapC } = makePath({
            ...common,
            data: dataC,
            yMin: MIN_Y,
            yMax: MAX_Y,
            maxMinFixed: true,
            smooth: false,
        });

        const { d: dD, dataMap: dataMapD } = makePath({
            ...common,
            data: dataD,
            yMin: MIN_Y,
            yMax: MAX_Y,
            maxMinFixed: true,
            smooth: false,
        });

        const {
            d: d2,
            minY: MIN_Y2 = 0,
            maxY: MAX_Y2 = 0,
            dataMap: dataMap2,
        } = makePath({
            ...common,
            data: data2,
            yMin: y2Min,
            yMax: y2Max,
            smooth: false,
        });

        const { d: d2B, dataMap: dataMap2B } = makePath({
            ...common,
            data: data2B,
            yMin: y2BMin,
            yMax: y2BMax,
            yRangeMin: range2bMin,
            yRangeMax: range2bMax,
            smooth: false,
        });

        const { d: d2C, dataMap: dataMap2C } = makePath({
            ...common,
            data: data2C,
            yMin: y2CMin,
            yMax: y2CMax,
            yRangeMin: range2cMin,
            yRangeMax: range2cMax,
            maxMinFixed: true,
            smooth: false,
        });

        const { d: d2D, dataMap: dataMap2D } = makePath({
            ...common,
            data: data2D,
            yMin: y2DMin,
            yMax: y2DMax,
            yRangeMin: range2dMin,
            yRangeMax: range2dMax,
            maxMinFixed: true,
            smooth: false,
        });

        const { d: d3 } = makePath({
            ...common,
            data: data3,
            yMin: y3Min,
            yMax: y3Max,
            yRangeMin: range3Min,
            yRangeMax: range3Max,
        });

        const X_TICKS = parseTicks(xTicks) || getTicks(tickCount, 0, MAX_X);
        const Y_TICKS = parseTicks(yTicks) || getTicks(tickCount, MIN_Y, MAX_Y, yTicksPrefix, yTicksPostfix).reverse();
        const Y_TICKS2 = parseTicks(y2Ticks) || getTicks(tickCount, MIN_Y2, MAX_Y2, y2TicksPrefix, y2TicksPostfix).reverse();

        const MOUSEPOS_TEXT_OFFSET = 10;

        return (
            <div class={style.lineChart} style={`height: ${height}px; overflow: hidden;`}>
                <svg xmlns='http://www.w3.org/2000/svg' class={style.svg} viewBox={`0 0 ${width} ${height}`}
                    preserveAspectRatio='none'
                    style={`padding-left: ${PADDING_LEFT}px; padding-bottom: ${PADDING_BOTTOM}px; width: calc(100%); max-height: 100%;`}
                    onMouseMove={this.mouseMove}
                    ref={c => this.svgRef = c}
                >
                    {d3 && <path d={d3} class={style.path3} />}
                    {d2D && <path d={d2D} class={style.path2D} />}
                    {d2C && <path d={d2C} class={style.path2C} />}
                    {d2B && <path d={d2B} class={style.path2B} />}
                    {d2 && <path d={d2} class={style.path2} />}
                    {dD && <path d={dD} class={style.pathD} />}
                    {dB && <path d={dB} class={style.pathB} />}
                    {dC && <path d={dC} class={style.pathC} />}
                    {d && <path d={d} class={style.path} />}

                    {/* {dCold && dCold.map(o => <line x1={o[0].x} y1={o[0].y} x2={o[1].x} y2={o[1].y} class={style.pathCold} />)};
                    {dWarm && dWarm.map(o => <line x1={o[0].x} y1={o[0].y} x2={o[1].x} y2={o[1].y} class={style.pathWarm} />)}; */}

                    {legend && <line x1='10' y1='5' x2='30' y2='5' class={style.path} />}
                    {legend && <text x='35' y='10' font-size='10px' class={style.pathText}>{legend}</text>}

                    {legendB && <line x1='10' y1='15' x2='30' y2='15' class={style.pathB} />}
                    {legendB && <text x='35' y='20' font-size='10px' class={style.pathText}>{legendB}</text>}

                    {legendC && <line x1='10' y1='25' x2='30' y2='25' class={style.pathC} />}
                    {legendC && <text x='35' y='30' font-size='10px' class={style.pathText}>{legendC}</text>}

                    {legendD && <line x1='10' y1='35' x2='30' y2='35' class={style.pathD} />}
                    {legendD && <text x='35' y='40' font-size='10px' class={style.pathText}>{legendD}</text>}


                    {legend2 && <line x1={width - 150} y1='5' x2={width - 130} y2='5' class={style.path2} />}
                    {legend2 && <text x={width - 125} y='10' font-size='10px' class={style.pathText}>{legend2}</text>}

                    {legend2B && <line x1={width - 150} y1='15' x2={width - 130} y2='15' class={style.path2B} />}
                    {legend2B && <text x={width - 125} y='20' font-size='10px' class={style.pathText}>{legend2B}</text>}

                    {legend2C && <line x1={width - 150} y1='25' x2={width - 130} y2='25' class={style.path2C} />}
                    {legend2C && <text x={width - 125} y='30' font-size='10px' class={style.pathText}>{legend2C}</text>}

                    {legend2D && <line x1={width - 150} y1='35' x2={width - 130} y2='35' class={style.path2C} />}
                    {legend2D && <text x={width - 125} y='30' font-size='10px' class={style.pathText}>{legend2D}</text>}


                    {legend3 && <line x1={width - 150} y1='25' x2={width - 130} y2='25' class={style.path3} />}
                    {legend3 && <text x={width - 125} y='30' font-size='10px' class={style.pathText}>{legend3}</text>}

                    {marker && <line x1={marker} y1={height} x2={marker} y2='0' class={style.marker} />}

                    {mousePos && mousePos.length > 0 && <line x1={mousePos[0]} y1={height} x2={mousePos[0]} y2='0' class={style.mouseLines} />}
                    {mousePos && mousePos.length > 0 && <line x1={0} y1={this.getMouseData(dataMap, mousePos[0], 'y')} x2={width} y2={this.getMouseData(dataMap, mousePos[0], 'y')} class={style.mouseLines} />}
                    
                    {mousePos && mousePos.length > 0 && dataMap && <circle cx={this.getMouseData(dataMap, mousePos[0], 'x')} cy={this.getMouseData(dataMap, mousePos[0], 'ySmooth')} r='5' class={style.path} />}
                    {mousePos && mousePos.length > 0 && dataMapB && <circle cx={this.getMouseData(dataMapB, mousePos[0], 'x')} cy={this.getMouseData(dataMapB, mousePos[0], 'ySmooth')} r='5' class={style.pathB} />}
                    {mousePos && mousePos.length > 0 && dataMapC && <circle cx={this.getMouseData(dataMapC, mousePos[0], 'x')} cy={this.getMouseData(dataMapC, mousePos[0], 'ySmooth')} r='5' class={style.pathC} />}
                    {mousePos && mousePos.length > 0 && dataMapD && <circle cx={this.getMouseData(dataMapD, mousePos[0], 'x')} cy={this.getMouseData(dataMapD, mousePos[0], 'ySmooth')} r='5' class={style.pathD} />}
                    {mousePos && mousePos.length > 0 && dataMap2 && <circle cx={this.getMouseData(dataMap2, mousePos[0], 'x')} cy={this.getMouseData(dataMap2, mousePos[0], 'ySmooth')} r='5' class={style.path2} />}
                    {mousePos && mousePos.length > 0 && dataMap2B && <circle cx={this.getMouseData(dataMap2B, mousePos[0], 'x')} cy={this.getMouseData(dataMap2B, mousePos[0], 'ySmooth')} r='5' class={style.path2B} />}
                    {mousePos && mousePos.length > 0 && dataMap2C && <circle cx={this.getMouseData(dataMap2C, mousePos[0], 'x')} cy={this.getMouseData(dataMap2C, mousePos[0], 'ySmooth')} r='5' class={style.path2C} />}
                    {mousePos && mousePos.length > 0 && dataMap2D && <circle cx={this.getMouseData(dataMap2D, mousePos[0], 'x')} cy={this.getMouseData(dataMap2D, mousePos[0], 'ySmooth')} r='5' class={style.path2D} />}

                    {mousePos && mousePos.length > 0 && <rect x={this.getMouseData(dataMap, mousePos[0], 'x', true, 10)} y={this.getMouseData(dataMap, mousePos[0], 'y', true, 10)} width='160' height='80' rx='5' ry='5' class={style.mouseHoverBox} />}
                    {mousePos && mousePos.length > 0 
                        && <text x={this.getMouseData(dataMap, mousePos[0], 'x', true)} y={this.getMouseData(dataMap, mousePos[0], 'y', true)} 
                            font-size='11px'
                            class={darkmode ? style.mouseHoverDarkmode : style.mouseHover}
                        >
                            <tspan x={this.getMouseData(dataMap, mousePos[0], 'x', true)} dy='1.2em'><tspan class={style.path}>--</tspan> {legend}: {this.getMouseData(dataMap, mousePos[0], 'yVal')} {unit}</tspan>
                            {dB && dataMapB && <tspan x={this.getMouseData(dataMapB, mousePos[0], 'x', true)} dy='1.2em'><tspan class={style.pathB}>--</tspan> {legendB}: {this.getMouseData(dataMapB, mousePos[0], 'yVal')} {unitB || unit}</tspan>}
                            {dC && dataMapC && <tspan x={this.getMouseData(dataMapC, mousePos[0], 'x', true)} dy='1.2em'><tspan class={style.pathC}>--</tspan> {legendC}: {this.getMouseData(dataMapC, mousePos[0], 'yVal')} {unitC || unit}</tspan>}
                            {dD && dataMapD && <tspan x={this.getMouseData(dataMapD, mousePos[0], 'x', true)} dy='1.2em'><tspan class={style.pathD}>--</tspan> {legendD}: {this.getMouseData(dataMapD, mousePos[0], 'yVal')} {unitD || unit}</tspan>}
                            {d2 && dataMap2 && <tspan x={this.getMouseData(dataMap2, mousePos[0], 'x', true)} dy='1.2em'><tspan class={style.path2}>--</tspan> {legend2}: {this.getMouseData(dataMap2, mousePos[0], 'yVal')} {unit2}</tspan>}
                            {d2B && dataMap2B && <tspan x={this.getMouseData(dataMap2B, mousePos[0], 'x', true)} dy='1.2em'><tspan class={style.path2B}>--</tspan> {legend2B}: {this.getMouseData(dataMap2B, mousePos[0], 'yVal')} {unit2B || unit2}</tspan>}
                            {d2C && dataMap2C && <tspan x={this.getMouseData(dataMap2C, mousePos[0], 'x', true)} dy='1.2em'><tspan class={style.path2C}>--</tspan> {legend2C}: {this.getMouseData(dataMap2C, mousePos[0], 'yVal')} {unit2C || unit2}</tspan>}
                            {d2D && dataMap2D && <tspan x={this.getMouseData(dataMap2D, mousePos[0], 'x', true)} dy='1.2em'><tspan class={style.path2D}>--</tspan> {legend2D}: {this.getMouseData(dataMap2D, mousePos[0], 'yVal')} {unit2D || unit2}</tspan>}

                        </text>}
                </svg>
                {showXTicks && (
                    <div class={style['x-axis']}
                        style={{
                            left: `${PADDING_LEFT}px`,
                            width: `calc(100% - ${PADDING_LEFT}px)`,
                        }}
                    >
                        {X_TICKS.map(v => <div data-value={transformXTick(v)} />)}
                    </div>
                )}
                {showYTicks && (
                    <div
                        class={style['y-axis']}
                        style={{
                            bottom: `${PADDING_BOTTOM}px`,
                        }}
                    >
                        {Y_TICKS.map(v => <div data-value={v} />)}
                    </div>
                )}
                {showY2Ticks && (
                    <div
                        class={style['y-axis2']}
                        style={{
                            bottom: `${PADDING_BOTTOM}px`,
                        }}
                    >
                        {Y_TICKS2.map(v => <div data-value={v} />)}
                    </div>
                )}
            </div>
        );
    }
}

export default LineChart;
